:root{
  --ink: #243746;         /* heading/navy like your site */
  --muted: #6f7b87;
  --brand: #3498db;       /* your primary blue */
  --surface: #ffffff;
  --bg: #f6f9fc;          /* section background (very light) */
  --line: #d8e8fb;        /* timeline line */
  --dot: #5b7ea6;         /* timeline dots */
  --shadow: 0 10px 24px rgba(0,0,0,.06);
}

/* full-bleed section (not a skinny centered column) */
.edu{
  padding: 4rem 2rem;
  background: var(--bg);
  font-family: 'Geneva'  /* ✅ clean body font */
}



/* Timeline spine (full-width layout) */
.edu-timeline{
  position: relative;
  width: 100%;
  padding-left: 3rem;       /* space for line/dots */
  padding-right: 1rem;      /* ✅ add a right gutter so content doesn't hit the edge */
  box-sizing: border-box;   /* ✅ include padding in width calc */
}

.edu-line{
  position: absolute;
  left: 1.5rem;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--line);
}

/* Each item spans the full available width */
.edu-item{
  position: relative;
  margin: 1.75rem 0;
}

.edu-dot {
  position: absolute;
  left: 1.06rem;
  top: 1.25rem;             /* aligns with card header */
  width: 14px;
  height: 14px;
  background: linear-gradient(135deg, #2d8dfd, #8a4dff); /* gradient */
  border: 3px solid #fff;
  border-radius: 50%;
  box-shadow: 0 2px 6px rgba(0,0,0,0.25); /* stronger for glow */
}

/* Full-width card (minus the left offset for the timeline) */
.edu-card{
  background: var(--surface);
  border-radius: 14px;
  box-shadow: var(--shadow);
  padding: 1.25rem 1.75rem;   /* a bit more inner right padding */
  margin-left: 3rem;          /* clears the spine/dot */
  margin-right: 1rem;         /* ✅ right gutter to keep date inside */
  width: calc(100% - 4rem);   /* ✅ 3rem (left) + 1rem (right) */
  box-sizing: border-box;     /* ✅ safe sizing */
  font-family: 'Geneva'; /* ✅ clean body font */
}

/* Header row: title + date right */
.edu-card__hdr{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
}

.edu-degree{
  font-size: 1.25rem;
  font-weight: 800;
  color: var(--ink);
  margin: 0;
  font-family: 'Nunito Sans SemiBold' /* ✅ heading font */
}

.gpa{
  font-weight: 700;
  color: #4b5865;
  margin-left: .4rem;
  font-family: 'Nunito Sans SemiBold';
}

.edu-date{
  color: #4b5865;
  font-weight: 700;
  white-space: nowrap;
  font-family: 'Nunito Sans SemiBold';
}

.edu-school{
  color: #4b5865;
  margin: .35rem 0 .9rem;
  font-size: 1.05rem;
  font-family: 'Nunito Sans SemiBold';
}

/* inline rows (Awards, Coursework) */
.edu-row{
  display: flex;
  align-items: center;
  gap: .75rem;
  flex-wrap: wrap;
  margin-top: .35rem;
  font-family: 'Inter', sans-serif;
}

.label{
  color: var(--ink);
  opacity: .8;
  min-width: 110px;
  font-family: 'Nunito Sans SemiBold';
}

/* Pill badges (blue, subtle) */
.badges{
  display: flex;
  gap: .55rem;
  row-gap: .55rem;
  flex-wrap: wrap;
}

.badge{
  display: inline-block;
  font-size: .9rem;
  padding: .35rem .7rem;
  background: #eef6ff;
  color: #244a6a;
  border: 1px solid #d5e9ff;
  border-radius: 999px;
  font-family: 'Nunito Sans SemiBold';
}

/* Responsive */
@media (max-width: 900px){
  .edu-card__hdr{
    flex-direction: column;
    align-items: flex-start;
    gap: .25rem;
  }
  .edu-date{
    margin-top: .15rem;
  }
}

.edu-title {
  font-size: 2.8rem;
  font-weight: 800;
  color: var(--ink);
  text-transform: capitalize;
  text-align: center;
  letter-spacing: .5px;
  margin: 0 0 2rem;

  /* force Poppins */
  font-family: 'Roboto';
}
