.skills {
  padding: 4rem 2rem;
  text-align: center;
  font-family: 'Inter', sans-serif; /* Ensure consistency */
}

.section-title {
  font-size: 2.3rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 0.5rem;
  font-family: 'Nunito Sans SemiBold';
}

.section-subtitle {
  font-size: 1rem;
  color: #637184;
  margin-bottom: 2.5rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.8rem;
}

.skill-card {
  background: #fff;
  padding: 2rem;
  border-radius: 14px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06);
  text-align: left;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.skill-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.1);
}

.skill-title {
  font-size: 1.2rem;
  font-family: 'Nunito Sans SemiBold';
  font-weight: 600;
  color: #243746;
  display: flex;
  align-items: center;
  gap: 0.6rem;
  margin-bottom: 1rem;
}

.skill-title span {
  font-size: 1.4rem;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
}

.skill-tag {
  background: #f5f7fb;
  color: #243746;
  font-size: 0.9rem;
  padding: 0.45rem 0.85rem;
  border-radius: 8px;
  font-weight: 500;
  transition: background 0.2s ease, transform 0.2s ease;
}

.skill-tag:hover {
  background: #2d8dfd;
  color: #fff;
  transform: scale(1.05);
}
