/* Projects.css (minimal + clean) */
.projects {
  padding: 4rem 0;
  background-color: #f8f9fa;
  font-family: 'Inter', sans-serif; /* body font */
}

.projects .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.projects .section-title {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: capitalize;       /* “Projects”, not ALL CAPS */
  letter-spacing: 1px;
  font-family: 'Roboto'; /* heading font */
}

/* grid + cards (keep your existing layout) */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.project-card {
  background-color: #fff;
  padding: 1.5rem 1.5rem 1.75rem;
  border-radius: 12px;
  box-shadow: 0 2px 10px rgba(0,0,0,.05);
  transition: transform .25s ease, box-shadow .25s ease;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto'; /* ensure card text uses Inter */
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 24px rgba(0,0,0,.10);
}

.project-image {
  width: 100%;
  aspect-ratio: 16 / 9;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 12px;
  display: block;
}

.project-card h3 {
  font-family: 'Nunito Sans SemiBold';
  font-size: 1.5rem;
  color: #2c3e50;
  margin: 0.5rem 0 0.25rem;
  line-height: 1.25;
 
}

.project-technologies {
  color: #7f8c8d;
  font-size: 0.95rem;
  margin-bottom: 0.75rem;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
}

.project-card ul {
  margin: 0 0 1.25rem;
  padding-left: 1.2rem;
  color: #555;
  line-height: 1.6;
  flex: 1;
  font-family: 'Roboto';
}

.project-card li + li { margin-top: 0.4rem; }

.project-card .btn {
  align-self: flex-start;
  padding: 0.6rem 1.1rem;
  background-color: transparent;
  color: #2c3e50;
  border: 2px solid #2c3e50;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all .2s ease;
  font-family: 'Nunito Sans SemiBold'
}

.project-card .btn:hover {
  background-color: #2c3e50;
  color: #fff;
}

.view-all { text-align: center; }
.view-all .btn {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background-color: #3498db;
  color: #fff;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  transition: background-color .2s ease;
}
.view-all .btn:hover { background-color: #2980b9; }

@media (max-width: 480px) {
  .project-card { padding: 1.25rem; }
  .project-card h3 { font-size: 1.25rem; }
}
