:root{
  --ink: #243746;
  --muted: #6f7b87;
  --brand: #3498db;
  --surface: #ffffff;
  --bg: #f6f9fc;
  --line: #d8e8fb;
  --dot: #5b7ea6;
  --shadow: 0 10px 24px rgba(0,0,0,.06);
}

/* section */
.exp{
  padding: 4rem 2rem;
  background: var(--bg);
}

/* full-width (not a narrow container) */
.experience .container{ max-width: 100%; padding: 0; }

/* heading */
.exp-title{
  font-size: 2.8rem;
  font-weight: 800;
  color: var(--ink);
  text-transform: capitalize;
  text-align: center;
  font-family: 'Robota';
  letter-spacing: .5px;
  margin: 0 0 2rem;
}

/* timeline spine */
.exp-timeline{
  position: relative;
  width: 100%;
  padding-left: 3rem;     
  padding-right: 1rem;    
  box-sizing: border-box;
}

.exp-line{
  position: absolute;
  left: 1.5rem;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--line);
}

/* item + dot */
.exp-item{
  position: relative;
  margin: 1.75rem 0;
}

.exp-dot{
   position: absolute;
  left: 1.06rem;
  top: 1.25rem;             /* aligns with card header */
  width: 14px;
  height: 14px;
  background: linear-gradient(135deg, #2d8dfd, #8a4dff); /* gradient */
  border: 3px solid #fff;
  border-radius: 50%;
  box-shadow: 0 2px 6px rgba(0,0,0,0.25); /* stronger for glow */
}

/* card */
.exp-card{
  background: var(--surface);
  border-radius: 14px;
  box-shadow: var(--shadow);
  padding: 1.25rem 1.75rem;       /* extra right padding */
  margin-left: 3rem;              /* clear spine */
  margin-right: 1rem;             /* right gutter */
  width: calc(100% - 4rem);       /* 3rem left + 1rem right */
  box-sizing: border-box;
  transition: transform .25s ease, box-shadow .25s ease;
}

.exp-card:hover{
  transform: translateY(-3px);
  box-shadow: 0 12px 26px rgba(0,0,0,.08);
}

/* header: role/org + date */
.exp-card__hdr{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
}

.exp-role{
  font-size: 1.4rem;
  font-family: 'Nunito Sans';
  font-weight: 800;
  color: var(--ink);
  margin: 0 0 .25rem;
}

.exp-org{
  margin: 0;
  color: #4b5865;
  font-size: 1.05rem;
  font-family: 'Nunito Sans'
}

/* date on right */
.exp-date{
  color: var(--muted);
  font-weight: 700;
  white-space: nowrap;
  color: var(--ink);
}

/* bullets */
.exp-list{
  margin: .85rem 0 0;
  padding-left: 1.25rem;
}

.exp-list li{
  margin-bottom: .6rem;
  line-height: 1.6;
  color: #555;
  position: relative;
  list-style: none;
  font-family: 'Nunito Sans';
  padding-left: .5rem;
}

.exp-list li::before{
  content: "•";
  color: #243746 ;
  font-weight: 900;
  position: absolute;
  left: -1.05rem;
}

/* responsive */
@media (max-width: 900px){
  .exp-card__hdr{
    flex-direction: column;
    align-items: flex-start;
    gap: .25rem;
  }
  .exp-date{
    margin-top: .1rem;
  }
  .exp-role{ font-size: 1.25rem; }
}
