:root { --nav-h: 64px; --brand1:#2d8dfd; --brand2:#845ef7; }

html, body { margin: 0; }
section { scroll-margin-top: calc(var(--nav-h) + 8px); }

/* Fixed bar */
.navbar {
  position: fixed; top: 0; left: 0; right: 0;
  height: var(--nav-h);
  background: #fff;
  border-bottom: 1px solid rgba(0,0,0,.08);
  z-index: 1000;
}

.nav-shell {
  height: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
}
.nav-shell {
  grid-template-columns: 1fr 1.2fr; /* makes the right column (links) take more space */
  justify-content: end;
}
/* Brand */
.brand {
  justify-self: start;
  background: none; border: 0;
  padding: 0; margin: 0;
  font-weight: 800; font-size: 1.25rem; cursor: pointer;
  color: #1e2a35;
}
.brand-accent {
  background: linear-gradient(90deg, var(--brand1), var(--brand2));
  -webkit-background-clip: text; background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Center nav */
.nav-center {
  justify-self: end;}
.links {
  list-style: none; margin: 0; padding: 0;
  display: flex; gap: 28px; align-items: center;
}
.links li { margin: 0; padding: 0; }
.links button {
  background: none; border: 0; cursor: pointer;
  font-weight: 600; color: #6b7b8c; padding: 6px 2px;  font-size: 1.1rem;
}
.links button:hover,
.links button.active { color: #1e2a35; }

/* Hamburger (mobile) */
.hamburger {
  display: none;
  background: none; border: 0; cursor: pointer;
  width: 32px; height: 24px; padding: 0; margin-right: 8px;
}
.hamburger span {
  display: block; width: 100%; height: 3px; background: #1e2a35; margin: 4px 0;
  transition: transform .25s ease, opacity .25s ease;
}
.hamburger.active span:nth-child(1) { transform: translateY(7px) rotate(45deg); }
.hamburger.active span:nth-child(2) { opacity: 0; }
.hamburger.active span:nth-child(3) { transform: translateY(-7px) rotate(-45deg); }

/* Mobile layout */
@media (max-width: 900px) {
  .nav-shell { grid-template-columns: auto auto 40px; gap: 8px; }
  .links {
    position: fixed; top: var(--nav-h); left: 0; right: 0;
    background: #fff; border-bottom: 1px solid rgba(0,0,0,.06);
    flex-direction: column; gap: 18px; padding: 16px 0;
    transform: translateY(-120%); transition: transform .25s ease;
  }
  .links.open { transform: translateY(0); }
  .hamburger { display: inline-block; }
}

/* Optional: push page content below fixed bar */
main, .page { padding-top: var(--nav-h); }
