@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&family=Poppins:wght@600;700;900&display=swap');

:root{
  --ink:#111827;
  --muted:#6b7280;
  --brand:#2d8dfd;
  --brand2:#a855f7;   /* purple for gradient */
  --border:rgba(0,0,0,.08);
}

/* SECTION WRAPPER */
.about-hero{
  padding: 110px 0 80px;
  background:
    radial-gradient(1100px 500px at 85% 0%, rgba(132,94,247,.18) 0%, transparent 60%),
    radial-gradient(900px 380px at 95% 20%, rgba(45,141,253,.18) 0%, transparent 60%),
    #f6fbff;
  font-family: 'Inter', sans-serif;
}

/* TWO-COLUMN LAYOUT */
.about-wrap{
  width:min(1200px, 92%);
  margin:0 auto;
  display:grid;
  grid-template-columns: 1.05fr .95fr;
  align-items:center;
  gap:56px;
}

/* LEFT COLUMN */
.about-copy{ text-align:left; }

/* HEADLINE */
.display{
  font-family: 'Poppins', sans-serif;
  font-size: clamp(2.2rem, 4.6vw, 3.6rem);
  font-weight: 800;
  line-height:1.1;
  margin-bottom: 1rem;
  color: var(--ink);
}

/* NAME with gradient */
.accent{
  background: linear-gradient(90deg, var(--brand), var(--brand2));
  -webkit-background-clip:text;
  background-clip:text;
  -webkit-text-fill-color:transparent;
}

/* INTRO TEXT */
.intro{
  font-size: 1.15rem;
  line-height: 1.7;
  color: var(--muted);
  margin-bottom: 1rem;
}

/* BULLET LIST */
.about-list{
  margin:18px 0 0;
  padding-left:18px;
  font-size: 1.05rem;
  color: var(--ink);
}
.about-list li{ margin:8px 0; }

/* CTA BUTTONS */
.about-cta{ display:flex; gap:14px; margin-top:24px; }
.btn-primary{
  display:inline-flex; align-items:center; justify-content:center;
  background: linear-gradient(90deg, var(--brand), var(--brand2));
  color:#fff; font-weight:700; border:none;
  padding:12px 20px; border-radius:12px; text-decoration:none;
  box-shadow:0 12px 28px rgba(45,141,253,.25);
}
.btn-primary:hover{ box-shadow:0 14px 32px rgba(45,141,253,.35); }
.btn-ghost{
  display:inline-flex; align-items:center; justify-content:center;
  background:#fff; color:#111; text-decoration:none;
  padding:12px 18px; border-radius:12px; border:1px solid var(--border);
  font-weight:700;
}

/* RIGHT COLUMN — CIRCLE PHOTO + GLOW */
.about-photo{
  position: relative;
  display:flex; justify-content:center; align-items:center;
  z-index:0;
}
.photo-ring{
  position: relative;
  width: 360px;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  overflow: hidden;
}
.photo-ring img{
  width:100%; height:100%; object-fit:cover;
  border-radius:50%; display:block;
}
/* glow */
.photo-ring::before{
  content:""; position:absolute; inset:-26px; border-radius:50%;
  background: radial-gradient(closest-side,
      rgba(132,94,247,.45),
      rgba(45,141,253,.35),
      transparent 70%);
  filter: blur(14px);
  z-index:-1;
}

/* RESPONSIVE */
@media (max-width: 960px){
  .about-wrap{ grid-template-columns: 1fr; gap:28px; }
  .about-photo{ order:-1; }
  .photo-ring{ width: 280px; }
}
